.footer {
  background-color: $color-footer-bg;
}

.footer-text {
  width: 482px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 30px;
  padding-bottom: 28px;
  color: $secondary-color-txt;
  display: flex;
  align-items: center;
  text-align: center;

  font-family: $font-family;
  font-style: normal;
  font-weight: $font-size-regular;
  font-size: 16px;
  line-height: 1.19;

  @media screen and (max-width: 767px) {
    flex-direction: column;
    font-size: 14px;
    line-height: 1.14;
    width: 236px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 767px) {
  .cocpyright {
    margin-bottom: 10px;
  }
}

// модальное окно Футера

.is-hidden {
  display: none;
}

body.modal-footer-open {
  overflow: hidden;
}

.footer-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-left: 15px;
  padding-right: 15px;
  background: rgba(0, 0, 0, 0.2);
  opacity: 1;
  transition: opacity 100ms cubic-bezier(0.4, 0, 0.2, 1);
  overflow-y: scroll;
}
.footer-backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.footer-backdrop.is-hidden .footer-modal {
  transform: translate(-50%, -50%) scale(1.3);
}

.footer-modal {
  position: absolute;
  left: 50%;
  top: 30px;
  transform: translate(-50%) scale(1);
  opacity: 1;
  z-index: 40;
  width: calc(100% - 2 * 15px);
  height: 100%;
  background-color: $color-footer-bg;
  border-radius: 24px;
  padding-top: 40px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 20px;
  line-height: 1.17;
  transition: transform 100ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 100ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer-close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: $color-modal-bg;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border: transparent;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    transform: scale(1.1);
  }
}

.team {
  background-color: $color-footer-bg;
}

.title-section {
  margin-bottom: 30px;
  margin-top: 0;
  font-family: $font-family;
  color: $secondary-color-txt;
  font-weight: $font-size-medium;
  font-size: 28px;
  line-height: 1.18;
  text-align: center;
  letter-spacing: 0.03em;
}

.team-list {
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-left: auto;
  margin-right: auto;
}

.team-img {
  display: block;
  max-width: 100%;
  height: auto;
}

.team-item {
  background-color: $color-modal-bg;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  margin-bottom: 30px;
  margin-right: 30px;
  border-radius: 0px 0px 4px 4px;
}

.link-icon {
  color: inherit;
  text-decoration: none;
}

.title-team {
  margin-top: 30px;
  margin-bottom: 10px;
  color: $secondary-color-txt;
  font-family: $font-family;
  font-weight: $font-size-medium;
  font-size: 16px;
  line-height: 1.19;
  letter-spacing: 0.03em;
  text-align: center;
}
